<template>
	<div class="addEdit-block" :style='{"padding":"30px"}'>
		<el-form
			:style='{"padding":"30px","boxShadow":"none","borderRadius":"6px","flexWrap":"wrap","display":"flex"}'
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="160px"
		>
			<template >
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食名称" prop="meishimingcheng">
					<el-input v-model="ruleForm.meishimingcheng" placeholder="美食名称" clearable  :readonly="ro.meishimingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="美食名称" prop="meishimingcheng">
					<el-input v-model="ruleForm.meishimingcheng" placeholder="美食名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="菜谱分类" prop="caipufenlei">
					<el-select :disabled="ro.caipufenlei" v-model="ruleForm.caipufenlei" placeholder="请选择菜谱分类" >
						<el-option
							v-for="(item,index) in caipufenleiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="菜谱分类" prop="caipufenlei">
					<el-input v-model="ruleForm.caipufenlei"
						placeholder="菜谱分类" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="原材料" prop="yuancailiao">
					<el-input v-model="ruleForm.yuancailiao" placeholder="原材料" clearable  :readonly="ro.yuancailiao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="原材料" prop="yuancailiao">
					<el-input v-model="ruleForm.yuancailiao" placeholder="原材料" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="配料名称" prop="peiliaomingcheng">
					<el-select :disabled="ro.peiliaomingcheng" v-model="ruleForm.peiliaomingcheng" placeholder="请选择配料名称" multiple filterable>
						<el-option
							v-for="(item,index) in peiliaomingchengOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="配料名称" prop="peiliaomingcheng">
					<el-input v-model="ruleForm.peiliaomingcheng"
						placeholder="配料名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="特色" prop="tese">
					<el-input v-model="ruleForm.tese" placeholder="特色" clearable  :readonly="ro.tese"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="特色" prop="tese">
					<el-input v-model="ruleForm.tese" placeholder="特色" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info' && !ro.meishitupian" label="美食图片" prop="meishitupian">
					<file-upload
						tip="点击上传美食图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.meishitupian?ruleForm.meishitupian:''"
						@change="meishitupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="upload" v-else-if="ruleForm.meishitupian" label="美食图片" prop="meishitupian">
					<img v-if="ruleForm.meishitupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.meishitupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.meishitupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info'&& !ro.zhizuoshipin" label="制作视频" prop="zhizuoshipin">
					<file-upload
						tip="点击上传制作视频"
						action="file/upload"
						:limit="1"
						:multiple="true"
						:fileUrls="ruleForm.zhizuoshipin?ruleForm.zhizuoshipin:''"
						@change="zhizuoshipinUploadChange"
					></file-upload>
				</el-form-item> 
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="ruleForm.zhizuoshipin" label="制作视频" prop="zhizuoshipin">
					<el-button :style='{"border":"2px solid #C8C8C8","cursor":"pointer","padding":"0 15px","margin":"0 20px 0 0","outline":"none","color":"#000","borderRadius":"0","background":"none","width":"400px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="text" size="small" @click="download($base.url+ruleForm.zhizuoshipin)">预览</el-button>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="!ruleForm.zhizuoshipin" label="制作视频" prop="zhizuoshipin">
					<el-button :style='{"border":"2px solid #C8C8C8","cursor":"pointer","padding":"0 15px","margin":"0 20px 0 0","outline":"none","color":"#000","borderRadius":"0","background":"none","width":"400px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="text" size="small">无</el-button>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="用户账号" prop="yonghuzhanghao">
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="用户账号" prop="yonghuzhanghao">
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="用户姓名" prop="yonghuxingming">
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="用户姓名" prop="yonghuxingming">
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="textarea" v-if="type!='info'" label="简介" prop="jianjie">
					<el-input
					  style="min-width: 200px; max-width: 600px;"
					  type="textarea"
					  :rows="8"
					  placeholder="简介"
					  v-model="ruleForm.jianjie" >
					</el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="ruleForm.jianjie" label="简介" prop="jianjie">
					<span :style='{"fontSize":"14px","lineHeight":"40px","minWidth":"400px","color":"#000","fontWeight":"500","display":"inline-block"}'>{{ruleForm.jianjie}}</span>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="type!='info'"  label="制作方法" prop="zhizuofangfa">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.zhizuofangfa" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else-if="ruleForm.zhizuofangfa" label="制作方法" prop="zhizuofangfa">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","minWidth":"400px","color":"#000","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.zhizuofangfa"></span>
                </el-form-item>
			<el-form-item :style='{"width":"100%","padding":"0","margin":"0","justifyContent":"center","display":"flex"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-tijiao20" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-quxiao08" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-fanhui21" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				meishimingcheng : false,
				caipufenlei : false,
				yuancailiao : false,
				peiliaomingcheng : false,
				tese : false,
				meishitupian : false,
				zhizuoshipin : false,
				jianjie : false,
				yonghuzhanghao : false,
				yonghuxingming : false,
				zhizuofangfa : false,
				thumbsupnum : false,
				crazilynum : false,
				clicktime : false,
				clicknum : false,
				discussnum : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				meishimingcheng: '',
				caipufenlei: '',
				yuancailiao: '',
				peiliaomingcheng: [],
				tese: '',
				meishitupian: '',
				zhizuoshipin: '',
				jianjie: '',
				yonghuzhanghao: '',
				yonghuxingming: '',
				zhizuofangfa: '',
				clicktime: '',
			},
		
			caipufenleiOptions: [],
			peiliaomingchengOptions: [],

			
			rules: {
				meishimingcheng: [
					{ required: true, message: '美食名称不能为空', trigger: 'blur' },
				],
				caipufenlei: [
				],
				yuancailiao: [
				],
				peiliaomingcheng: [
				],
				tese: [
				],
				meishitupian: [
				],
				zhizuoshipin: [
				],
				jianjie: [
				],
				yonghuzhanghao: [
				],
				yonghuxingming: [
				],
				zhizuofangfa: [
				],
				thumbsupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				crazilynum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				clicktime: [
				],
				clicknum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				discussnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='meishimingcheng'){
							this.ruleForm.meishimingcheng = obj[o];
							this.ro.meishimingcheng = true;
							continue;
						}
						if(o=='caipufenlei'){
							this.ruleForm.caipufenlei = obj[o];
							this.ro.caipufenlei = true;
							continue;
						}
						if(o=='yuancailiao'){
							this.ruleForm.yuancailiao = obj[o];
							this.ro.yuancailiao = true;
							continue;
						}
						if(o=='peiliaomingcheng'){
							this.ruleForm.peiliaomingcheng = obj[o];
							this.ro.peiliaomingcheng = true;
							continue;
						}
						if(o=='tese'){
							this.ruleForm.tese = obj[o];
							this.ro.tese = true;
							continue;
						}
						if(o=='meishitupian'){
							this.ruleForm.meishitupian = obj[o];
							this.ro.meishitupian = true;
							continue;
						}
						if(o=='zhizuoshipin'){
							this.ruleForm.zhizuoshipin = obj[o];
							this.ro.zhizuoshipin = true;
							continue;
						}
						if(o=='jianjie'){
							this.ruleForm.jianjie = obj[o];
							this.ro.jianjie = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='zhizuofangfa'){
							this.ruleForm.zhizuofangfa = obj[o];
							this.ro.zhizuofangfa = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
				

















			}
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
					if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
						this.ro.yonghuzhanghao = true;
					}
					if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.yonghuxingming = json.yonghuxingming
						this.ro.yonghuxingming = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
            this.$http({
				url: `option/caipufenlei/caipufenlei`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.caipufenleiOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
            this.$http({
				url: `option/peiliaoxinxi/peiliaomingcheng`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.peiliaomingchengOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `caipuxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.peiliaomingcheng = this.ruleForm.peiliaomingcheng.split(",");
        this.ruleForm.zhizuofangfa = this.ruleForm.zhizuofangfa.replace(reg,'../../../djangojpebv4m0/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {






	if(this.ruleForm.meishitupian!=null) {
		this.ruleForm.meishitupian = this.ruleForm.meishitupian.replace(new RegExp(this.$base.url,"g"),"");
	}

	if(this.ruleForm.zhizuoshipin!=null) {
		this.ruleForm.zhizuoshipin = this.ruleForm.zhizuoshipin.replace(new RegExp(this.$base.url,"g"),"");
	}











var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				this.ruleForm.peiliaomingcheng = this.ruleForm.peiliaomingcheng.join(",");
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "caipuxinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `caipuxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.caipuxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `caipuxinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.caipuxinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.caipuxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    meishitupianUploadChange(fileUrls) {
	    this.ruleForm.meishitupian = fileUrls;
    },
    zhizuoshipinUploadChange(fileUrls) {
	    this.ruleForm.zhizuoshipin = fileUrls;
    },
  }
};
</script>
